body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Header-background {
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
}


.navbar-brand {
    font-family: "Arial Black", Gadget, sans-serif;
    font-weight: bold;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #fff;
}

.bar-item {
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.bar-item:hover {
    color:darkorange;
}
h1 {
    font: 700 40px/1.2 Raleway, sans-serif;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin: 5px 0 5px;
}

.introduction {
    font-family: Raleway, "Times New Roman", Times, sans-serif;
    font-size: 22px;
    font-style: normal;
    text-transform: none;
    margin: 15px 0;
    color: #00FF00;
}
h2 {
    font-family: Raleway, sans-serif;
    font-weight: 800;
    font-size: 30px;
    letter-spacing: 5px;
    display: block;
    text-transform: uppercase;
}

h4 {
    font-family: Raleway, "Times New Roman", Times, sans-serif;
    font-style: normal;
    font-size: 18px;
    color: grey;
}

.card-title {
    color: black;
    font-size: 22px;
}

CardDate {
    color: darkgray;
}

.card-text {
    color: grey;
}

.about-section {
    background-color: #f5f5f5;
    padding-top: 50px;
    padding-bottom: 50px;
}

.about-card {
    height: 400px;
}

.card-body {
    padding-top: 50px;
    padding-bottom: 50px;
}

.row-padding {
    padding-top: 25px;
    padding-bottom: 25px;
}

i {
    padding-bottom: 20px;
}

.card {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}
